<template>
    <div id="ticketPage" class="page">
        <div class="card">
            <div class="cardTitle"></div>
            <div class="toolsBar">
                <div class="row">
                    <div class="btnGroup">
                        <buttonLib btnClass="btn" text="" @click="newTicket"/>
                        <buttonLib btnClass="btn" text="" @click="getData(limit,1)"/>
                    </div>                    
                </div>
            </div>

            <div :class="loading?'tableBox loadingLocal':'tableBox'">
                <div class="empty" v-if="tableData.length==0"></div>
                <div class="tableLine" v-for="(item,key) in tableData" :key="'tableData_'+key">
                    <el-table :data="[item]" border style="width: 100%" :show-header="false"  row-class-name="tableTitle">
                        <el-table-column class-name="no-bottom-border" :resizable="false">
                            <template slot-scope="scope">
                                {{$root.shortStr(scope.row.title)}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="status" width="90" class-name="no-bottom-border" :resizable="false">
                            <template slot-scope="scope">
                                <el-tag v-if="scope.row.status===0" type="info"></el-tag>
                                <el-tag v-if="scope.row.status===1" type="success"></el-tag>
                                <el-tag v-if="scope.row.status===2" type="danger"></el-tag>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-table :data="[item]" border style="width: 100%" size="mini">
                        <el-table-column label="" :resizable="false">
                            <template slot-scope="scope">
                                <div class="btnGroup">
                                    <buttonLib @click="showTicket(scope.row)" btnClass="rowBtn" size="small" text="" />
                                    <buttonLib v-if="scope.row.status===0||scope.row.status===1" @click="closeTicket(scope.row)" btnClass="rowBtn" size="small" text="" />
                                    <buttonLib v-if="scope.row.status===2" @click="delTicket(scope.row)" btnClass="rowBtn" size="small" text="" />
                                </div>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
                <div class="pagenation">
                    <pageLib :total="totalPage" :page="currentPage" @change="changePage"/>
                </div>
            </div>

            <!-- S -->
            <dialogLib ref="detail" v-if="showTicketContent" title="" @hide="closeDetail" width="calc(100vw - 20px);" height="calc(100vh - 40px);" maxHeight="calc(100vh - 40px);">
                <div ref="detailContent" :class="dialogLoading?'detail loadingLocal':'detail'">
                    <div class="title">:{{ detailData[0]?.no }}</div>
                    <div v-for="(row,key) in detailData" :key="'detail_'+key" :class="row.role=='user'?'row right':'row'">
                        <div class="role">
                            {{ row.role=='user'?'':'' }} {{ $root.getDate(row.createtime) }}
                        </div>
                        <div class="msg">
                            {{ row.content }}
                        </div>
                    </div>
                </div>
                <div class="inputBox">
                    <textarea rows="6" :class="disabled?'disabled':''" :disabled="disabled" v-model="reForm.content" placeholder=""></textarea>
                    <div :class="disabled?'send disabled':'send'" @click="replyBtn">
                        
                    </div>
                </div>
            </dialogLib>
             <!-- E -->

             <!-- hcaptcha S -->
            <dialogLib v-if="isShowHcaptcha" title="" :hideWithMask="false" @hide="closeHcaptcha" height="auto" max-width="400px" >
                <vue-hcaptcha @verify="hcapResult" :sitekey="hCaptchaKey" ></vue-hcaptcha>
            </dialogLib>
            <!-- hcaptcha E -->

            <!-- S -->
             <dialogLib ref="confirm" v-if="isShowConfirm" title="" @hide="hideConfirm" :isShowClose="false" height="auto" max-width="400px" >
                <div class="confirm">
                    <div class="row">
                        <div class="title">{{ confirmTitle }}</div>
                    </div>
                    <div class="row">
                        <div class="content" v-html="confirmContent"></div>
                    </div>
                    <div class="row">
                        <buttonLib btnClass="btn yes" text="" @click="confirmYes"/>
                        <buttonLib btnClass="btn no" text="" @click="confirmNo"/>
                    </div>
                </div>
             </dialogLib>
             <!-- E -->

             <!-- S -->
            <dialogLib ref="newTicket" v-if="isShowNewTicket" title="" @hide="hideNewTicket" height="auto" max-height="80vh" max-width="400px" >
                <div class="newTicket">
                    <div class="row">
                        <el-alert
                            title=""
                            type="error"
                            effect="dark"
                            :closable="false">
                        </el-alert>
                    </div>
                    <div class="row"> 
                        <textarea rows="10" v-model="newTicketFrom.content" placeholder=""></textarea>
                    </div>
                    <div class="row">
                        <buttonLib btnClass="btn" text="" @click="newTicketBtn"/>
                    </div>
                </div>
             </dialogLib>
             <!-- E -->
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import VueHcaptcha from '@hcaptcha/vue-hcaptcha';

import buttonLib from '@/components/lib/unit/buttonLib.vue';
import pageLib from '@/components/lib/unit/pageLib.vue';
import dialogLib from '@/components/lib/unit/dialogLib.vue';

export default {
    name: 'ticketPage',
    components: {
        buttonLib,
        pageLib,
        dialogLib,
        VueHcaptcha
    },
    data() {
        return {
            loading:false,
            dialogLoading:false,
            URL: window.config.apiUrl,
            axiosC: new AbortController(),    // 
            tableData:[],
            totalPage:1,
            currentPage:1,
            limit:10,
            detailData:[],
            showTicketContent:false,
            disabled:false,
            isShowConfirm:false,
            confirmTitle:"",
            confirmContent:"",
            confirmAction:function(){
                console.log("");
            },

            reForm:{
                code:"",
                no:"",
                content:""
            },
            hCaptchaKey : window.config.hCaptcha,
            hCaptchaType:'newTicket',

            isShowHcaptcha:false,
            isShowNewTicket:false,
            newTicketFrom:{
                code:"",
                content:""
            }
        }
    },
    methods: {
        //
        async submitTicket(){ 
            let data = null;
            this.$root.loading(true);
            try {
                data = await axios.post(
                    this.URL + "ticket/submit",
                    this.newTicketFrom,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.$root.loading(false);
            this.$refs.newTicket?.hide();
            if(data?.data?.code != 1){
                this.$notify.error({
                    title: "",
                    message: data?.data?.msg,
                });
                return;
            }
            this.$notify.success({
                title: "",
                message: "",
            });
            this.getData(this.limit,1);
        },
        //
        newTicketBtn(){
            if(this.newTicketFrom?.content == ""){
                this.$notify.error({
                    title: "",
                    message: "",
                });
                return;
            }
            this.hCaptchaType = "new";
            this.isShowHcaptcha = true;
        },
        //
        newTicket(){
            this.isShowNewTicket = true;
        },
        //
        hideNewTicket(){
            this.isShowNewTicket = false;
            this.newTicketFrom.content = "";
        },
        //
        hideConfirm(){
            this.isShowConfirm = false;
        },
        //
        confirmYes(){
            this.confirmAction();
            this.$refs.confirm?.hide();
        },
        //
        confirmNo(){
            this.$refs.confirm?.hide();
        },
        //
        replyBtn(){
            if(this.reForm?.content == ""){
                this.$notify.error({
                    title: "",
                    message: "",
                });
                return;
            }
            this.hCaptchaType = "reply";
            this.isShowHcaptcha = true;
        },
        //hcaptcha
        closeHcaptcha(){
            this.isShowHcaptcha = false;
        },
        //hcaptcha
        async hcapResult(token){
            if(this.hCaptchaType == 'reply'){
                this.reForm.code = token;
                this.reForm.no = this.detailData[0]?.no;
            }
            if(this.hCaptchaType == 'new'){
                this.newTicketFrom.code = token;
            }
            if(token){
                this.closeHcaptcha();
                if(this.hCaptchaType == 'reply'){
                    await this.sendReply();
                    this.reForm = {
                        code:"",
                        no:"",
                        content:""
                    };
                    this.$refs.detail?.hide();
                }
                if(this.hCaptchaType == 'new'){
                    await this.submitTicket();
                    this.newTicketFrom = {
                        code:"",
                        content:""
                    };
                    this.$refs.newTicket?.hide();
                }                
                this.getData(this.limit,1); 
            }
        },
        //
        async sendReply(){ 
            let data = null;
            this.$root.loading(true);
            try {
                data = await axios.post(
                    this.URL + "ticket/reply",
                    this.reForm,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.$root.loading(false);
            if(data?.data?.code!=1){
                this.$notify.error({
                    title: '',
                    message: data?.data?.msg,
                });
                return;
            }
           
            this.$notify.error({
                title: '',
                message: data?.data?.msg,
            });
        },
        //
        closeTicket(row){
            //
            this.confirmAction = function(){
                this.doTicketAction(row,"close");
            }
            this.confirmTitle=""
            this.confirmContent=":<br><span style='color:red;'>"+row?.no+"</span><br>,";
            this.isShowConfirm = true;
        },
        //
        delTicket(row){
            //
            this.confirmAction = function(){
                this.doTicketAction(row,"del");
            }
            this.confirmTitle=""
            this.confirmContent=":<br><span style='color:red;'>"+row?.no+"</span><br>,.";
            this.isShowConfirm = true;
        },
        //
        async doTicketAction(row,action="close"){ 
            let data = null;
            let postData = {
                no: row?.no,
                action: action,
            };
            this.$root.loading(true);
            try {
                data = await axios.post(
                    this.URL + "ticket/close",
                    postData,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.$root.loading(false);
            if(data?.data?.code!=1){
                this.$notify.error({
                    title: '',
                    message: data?.data?.msg,
                });
                return;
            }
            this.$notify.success({
                title: '',
                message: data?.data?.msg,
            });
            this.getData(this.limit,1);
        },
        //
        closeDetail(){
            this.showTicketContent = false;
            this.detailData = [];
        },
        //
        showTicket(row){
            this.showTicketContent = true;
            this.disabled = row?.status == 2;
            this.getDetail(row.no);
        },
        changePage(page){
            this.getData(this.limit,page);
        },
        //
        async getDetail(no){
            let data = null;
            this.dialogLoading = true;
            try {
                data = await axios.get(
                    this.URL + "ticket/view?no="+no,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.dialogLoading = false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.dialogLoading = false;
            if(data?.data?.code != 1){
                this.$notify.error({
                    title: '',
                    message: data?.data?.msg,
                });
                return;
            }
            this.detailData = data?.data?.data;
            //
            if(this.$refs.detailContent){
                this.$nextTick(() => {
                    //
                    this.$refs.detailContent.scrollTo({
                        top: this.$refs.detailContent.scrollHeight,
                        behavior: 'smooth'
                    });
                });
            }
        },
        //
        async getData(limit=10,page=1) {
            this.loading = true;
            this.tableData = [];
            this.currentPage = page;

            let data = null;
            try {
                data = await axios.get(
                    this.URL + "ticket/list?page="+page+"&limit="+limit,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.loading = false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.loading = false;
            let result = data?.data?.data;
            this.totalPage = result?.last_page;
            this.tableData = result?.data;
        },

    },
    mounted() {
        this.$nextTick(() => { 
            this.getData(this.limit,1);
        });
    },
    beforeDestroy() {
        this.axiosC.abort();
    }
}
</script>

<style scoped lang="scss" src="@/assets/css/view/ticketPage.scss">

</style>