<template>
    <div id="userPage" class="page">
        <el-tabs v-model="activeName">
            <el-tab-pane label="" name="usecard">
                <useCard @finish="reloadData"/>
            </el-tab-pane>

            <el-tab-pane label="" name="cardlist">
                <!--  -->
                <div class="cardTable card">
                <div class="cardTitle"></div>
                <div class="toolsBar">
                    <div class="row">
                        <div class="title"></div>
                    </div>
                    <div class="row">
                        <input type="text" placeholder="" v-model="searchCode" clearable/>
                    </div>
                    <div class="row">
                        <div class="btnGroup">
                            <buttonLib btnClass="btn" text="" @click="reloadData()"/>
                            <buttonLib btnClass="btn" text="" @click="searchCode=''"/>
                        </div>
                    </div>
                </div>
                <div :class="loading?'tableBox loadingLocal':'tableBox'">
                    <el-table :data="cardsData" border style="width: 100%" row-class-name="tableRow" @sort-change="sortChange">
                        <el-table-column type="expand" :resizable="false">
                            <template slot-scope="scope">
                                <div class="expand">
                                    <div class="item">
                                        <span class="lable">:</span>
                                        <span class="value">{{scope.row.name}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">:</span><br>
                                        <span class="value">{{scope.row.code}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">():</span>
                                        <span class="value">{{scope.row.tunnels_num}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">(MB):</span>
                                        <span class="value">{{scope.row.traffic_amount}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">:</span>
                                        <span class="value">{{ scope.row?.level_text?scope.row.level_text:'' }}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">:</span>
                                        <span class="value">{{$root.getDate(scope.row.exptime)}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">:</span>
                                        <span class="value">{{$root.getDate(scope.row.usetime)}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">():</span>
                                        <span class="value">{{scope.row.tunnels_num}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">():</span>
                                        <span class="value">{{scope.row.tunnels_num}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">():</span>
                                        <span class="value">{{scope.row.tunnels_num}}</span>
                                    </div>
                                </div>                                 
                            </template>
                        </el-table-column>
                        <el-table-column prop="name" label="" sortable="custom" :resizable="false">
                        </el-table-column>

                        <el-table-column prop="status" label="" sortable="custom" :resizable="false">
                            <template slot-scope="scope">
                                <el-tag v-if="scope.row.status == 1" type="success"></el-tag>
                                <el-tag v-if="scope.row.status == 2" type="danger"></el-tag>
                                <el-tag v-if="scope.row.status == 3" type="info"></el-tag>
                                <el-tag v-if="scope.row.traffic_status&&scope.row.traffic_status != ''" type="danger"></el-tag>
                            </template>
                        </el-table-column>
                    </el-table>
                    <div class="pagenation">
                        <pageLib :total="totalPage" :page="currentPage" @change="changePage"/>
                    </div>
                </div>
                </div>
                <!--  -->
            </el-tab-pane>

            <el-tab-pane label="" name="userdata">
                <userData :userInfo="userInfo"/>
            </el-tab-pane>

            <el-tab-pane label="" name="userconfig">
                <!-- S -->
                <div class="controller card">
                <div class="cardTitle"></div>
                <ul>
                    <li>
                        <div class="controllerBtn" @click="isShowChangepwd=true"></div>
                    </li>
                    <li>
                        <div class="controllerBtn" @click="isShowChangeemail=true"></div>
                    </li>
                    <li>
                        <div class="line">
                            
                            <el-tooltip
                            class="help"
                            effect="dark"
                            content=","
                            placement="top">
                                <i class="icon el-icon-question"></i>
                            </el-tooltip>                            
                        </div>
                    </li>
                    <li>
                        <div class="switch">
                            <el-switch
                                v-model="isOpenTrafficAlert"
                                active-color="#b81010"
                                inactive-color="#C0C4CC">
                            </el-switch>
                            <span>{{isOpenTrafficAlert?'':''}}</span>
                        </div>
                        <div class="inputBox" v-if="isOpenTrafficAlert">
                            <div class="pre"></div>
                            <input class="hasAppend hasPre" type="number" v-model="notifyConfigForm.traffic" placeholder="(MB)"/>
                            <div class="append">MB</div>
                        </div>
                        <buttonLib v-if="isOpenTrafficAlert" class="saveBtn" size="small" text="" @click="saveNotifyConfig"/>
                    </li>
                    <li>
                        <div class="switch">
                            <el-switch
                                v-model="isOpenEXPAlert"
                                active-color="#b81010"
                                inactive-color="#C0C4CC">
                            </el-switch>
                            <span>{{isOpenEXPAlert?'':''}}</span>
                        </div>
                        <div class="inputBox" v-if="isOpenEXPAlert">
                            <div class="pre"></div>
                            <input class="hasAppend hasPre" type="number" v-model="notifyConfigForm.cardday" placeholder=""/>
                            <div class="append"></div>
                        </div>
                        <buttonLib v-if="isOpenEXPAlert" class="saveBtn" size="small" text="" @click="saveNotifyConfig"/>
                    </li>
                </ul>
                </div>
                <!-- E -->

                <!-- S -->
                <dialogLib ref="changepwd" v-if="isShowChangepwd" title="" maxWidth="400px" height="auto" @hide="isShowChangepwd=false">
                    <div class="changepwd">
                        <div class="item">
                            <el-alert
                                title=","
                                type="info"
                                :closable="false"
                                effect="dark">
                            </el-alert>
                        </div>
                        <div class="item">
                            <div class="lable"></div>
                            <div class="row">
                                <div class="value">
                                    <input v-model="changepwdForm.code" placeholder=""/>
                                </div>
                                <buttonLib btnClass="btn" text="" @click="getpwdToken"/>
                            </div>
                        </div>
                        <div class="item">
                            <div class="lable"></div>
                            <div class="row">                        
                                <div class="value">
                                    <input type="password" v-model="changepwdForm.newpwd" placeholder=""/>
                                </div>
                            </div>
                        </div>
                        <div class="item">
                            <div class="lable"></div>
                            <div class="row">                        
                                <div class="value">
                                    <input type="password" v-model="changepwdForm.confirmpwd" placeholder=""/>
                                </div>
                            </div>
                        </div>
                        <div class="btnBox">
                            <buttonLib btnClass="btn" text="" @click="changepwd"/>
                        </div>
                    </div>
                </dialogLib>
                <!-- E -->

                <!-- S -->
                <dialogLib ref="changeemail" v-if="isShowChangeemail" title="" maxWidth="400px" height="auto" @hide="isShowChangeemail=false">
                    <div class="changeemail">
                        <div class="item">
                            <el-alert
                                title=","
                                type="info"
                                :closable="false"
                                effect="dark">
                            </el-alert>
                        </div>
                        <div class="item">
                            <div class="lable"></div>
                            <div class="row">
                                <div class="value">
                                    <input v-model="changeemailForm.code" placeholder=""/>
                                </div>
                                <buttonLib btnClass="btn" text="" @click="getemailToken('changeemailS1')"/>
                            </div>
                        </div>
                        <div class="item">
                            <div class="lable"></div>
                            <div class="row">
                                <div class="value">
                                    <input v-model="changeemailForm.email" placeholder=""/>
                                </div>
                                <buttonLib btnClass="btn" text="" @click="getemailToken('changeemailS2')"/>
                            </div>
                        </div>
                        <div class="item">
                            <div class="lable"></div>
                            <div class="row">
                                <div class="value">
                                    <input v-model="changeemailForm.newcode" placeholder=""/>
                                </div>
                            </div>
                        </div>
                        <div class="btnBox">
                            <buttonLib btnClass="btn" text="" @click="changeEmail()"/>
                        </div>
                    </div>
                </dialogLib>
                <!-- E -->
            </el-tab-pane>
        </el-tabs>
        <!-- hcaptcha S -->
        <dialogLib ref="hcaptcha" v-if="isShowHcaptcha" title="" :hideWithMask="false" @hide="closeHcaptcha" height="auto" max-width="400px" >
            <vue-hcaptcha @verify="hcapResult" :sitekey="hCaptchaKey" ></vue-hcaptcha>
        </dialogLib>
        <!-- hcaptcha E -->
    </div>
</template>

<script>
import axios from 'axios';
import VueHcaptcha from '@hcaptcha/vue-hcaptcha';

import userData from '@/components/lib/userData.vue';
import useCard from '@/components/lib/useCard.vue';
import pageLib from '@/components/lib/unit/pageLib.vue';
import buttonLib from '@/components/lib/unit/buttonLib.vue';
import dialogLib from '@/components/lib/unit/dialogLib.vue';

export default {
    name: 'userPage',
    components: {
        userData,
        useCard,
        pageLib,
        buttonLib,
        dialogLib,
        VueHcaptcha
    },
    data() {
        return {
            activeName:"userdata",
            userInfo: null,
            cardsData:[],
            totalPage:1,
            currentPage:1,
            limit:10,
            loading:false,
            URL:window.config.apiUrl,
            axiosC: new AbortController(),    // 
            sort:"status",
            order:"asc",
            searchCode:"",
            isShowChangepwd:false,
            changepwdForm:{
                code:"",
                newpwd:"",
                confirmpwd:""
            },
            isShowChangeemail:false,
            changeemailForm:{
                code:"",
                newcode:"",
                email:"",
            },
            isOpenTrafficAlert:false,
            isOpenEXPAlert:false,
            isShowHcaptcha:false,
            hCaptchaType:"changepwd",
            hCaptchaKey : window.config.hCaptcha,
            notifyConfigForm:{
                traffic:200,    //MB
                cardday:3,      //
            },
        }
    },
    watch: {
        searchCode:"debouncedGetData",
        userInfo:{
            handler(val){
                if(val){
                    this.isOpenTrafficAlert = val?.traffic_alert > 0;
                    this.isOpenEXPAlert = val?.cards_alert > 0;
                    this.notifyConfigForm = {
                        traffic:val?.traffic_alert > 0 ? val?.traffic_alert/1024 : 0,
                        cardday:val?.cards_alert > 0 ? val?.cards_alert : 0
                    }
                }
                else{
                    this.isOpenTrafficAlert = false;
                    this.isOpenEXPAlert = false;
                    this.notifyConfigForm = {
                        traffic:0,
                        cardday:0
                    }
                }
            },
            deep: true,
            immediate: true
        },
        "isOpenTrafficAlert":function(val){ 
            if(!val){
                this.notifyConfigForm.traffic = 0;
                this.saveNotifyConfig();
            }
        },
        "isOpenEXPAlert":function(val){ 
            if(!val){
                this.notifyConfigForm.cardday = 0;
                this.saveNotifyConfig();
            }
        },
    },
    methods: {
        //
        async saveNotifyConfig(){
            let data = null;
            try{
                data = await axios.post(this.URL+"/user/changeNotice",
                    this.notifyConfigForm,
                    {
                        signal: this.axiosC.signal,
                        headers: { token: localStorage.getItem("token") ?? "" },
                    })
            }
            catch(err){
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('');
                }
                else {
                    this.$root.netError(err);
                }
                this.closeChangeEmail();
                return;
            }
            if(data?.data?.code!=1){
                this.$notify.error({
                    title: '',
                    message: data?.data?.msg,
                });
                return;
            }           
            this.$notify.error({
                title: '',
                message: data?.data?.msg,
            });
        },
        //
        async changeEmail() { 
            //S
            if(this.changeemailForm.code=="" || this.changeemailForm.newcode=="" || this.changeemailForm.email==""){
                this.$notify.error({
                    title: '',
                    message:""
                });
                return;
            }
            //E
            this.$root.loading(true);
            let data = null;
            let postData = {
                code:this.changeemailForm.code,
                newcode:this.changeemailForm.newcode,
                email:this.changeemailForm.email
            }
            try{
                data = await axios.post(this.URL+"/user/changeemail",
                    postData,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch(err){
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('');
                }
                else {
                    this.$root.netError(err);
                }
                this.closeChangeEmail();
                return;
            }
            this.closeChangeEmail();
            this.$root.loading(false);
            if(data?.data?.code!=1){
                this.$notify.error({
                    title: '',
                    message: data?.data?.msg,
                });
                return;
            }           
            this.$notify.error({
                title: '',
                message: data?.data?.msg,
            });
        },
        //
        closeChangeEmail() {
            this.changeemailForm = {
                code:"",
                newcode:"",
                email:""
            };
            this.$refs?.changeemail?.hide();
        },
        //
        closeChangePwd() {
            this.changepwdForm = {
                code:"",
                newpwd:"",
                confirmpwd:""
            };
            this.$refs?.changepwd?.hide();
        },
        //
        async changepwd() { 
            //S
            if(this.changepwdForm.code=="" || this.changepwdForm.newpwd=="" || this.changepwdForm.confirmpwd==""){
                this.$notify.error({
                    title: '',
                    message:""
                });
                return;
            }
            if(this.changepwdForm.newpwd!=this.changepwdForm.confirmpwd){
                this.$notify.error({
                    title: '',
                    message:""
                });
                return;
            }
            //E
            this.$root.loading(true);
            let data = null;
            let postData = {
                code:this.changepwdForm.code,
                newpassword:this.changepwdForm.newpwd,
            }
            try{
                data = await axios.post(this.URL+"/user/changepwd",
                    postData,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch(err){
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('');
                }
                else {
                    this.$root.netError(err);
                }
                this.closeChangePwd();
                return;
            }
            this.closeChangePwd();
            this.$root.loading(false);
            if(data?.data?.code!=1){
                this.$notify.error({
                    title: '',
                    message: data?.data?.msg,
                });
                return;
            }           
            this.$notify.error({
                title: '',
                message: data?.data?.msg,
            });
        },
        //
        async sendEmailCode(event="",code=""){
            this.$root.loading(true);
            let data = null;
            let postData = {
                id:event,
                code:code
            }
            if(event=="changeemailS2"){
                postData.email = this.changeemailForm.email;
            }
            try{
                data = await axios.post(this.URL+"/user/sendMail",
                    postData,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch(err){
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.$root.loading(false);
            if(data?.data?.code!=1){
                this.$notify.error({
                    title: '',
                    message: data?.data?.msg,
                });
                return;
            }           
            this.$notify.error({
                title: '',
                message: data?.data?.msg,
            });
        },
        //hcaptcha token
        getpwdToken(){
            this.isShowHcaptcha = true;
            this.hCaptchaType = 'changepwd';
        },
        //hcaptcha token
        getemailToken(event=""){
            this.isShowHcaptcha = true;
            this.hCaptchaType = event;
        },
        //hcaptcha
        closeHcaptcha(){
            this.isShowHcaptcha = false;     
        },
        //hcaptcha
        async hcapResult(token){
            if(token){
                this.$refs?.hcaptcha?.hide();
                this.sendEmailCode(this.hCaptchaType,token);
            }
        },
        sortChange(column){
            this.sort = column.prop;
            if(column.prop == "level_text"){
                this.sort = "level_id";
            }
            if(column.order == "descending"){
                this.order = "desc";
            }else{
                this.order = "asc";
            }
            this.getData();
        },
        formatTraffic(full=0, used=0) {
            if(!used){
                used = 0;
            }
            if(!full){
                full = 0;
            }
            let value = 0;
            if(full > used){
                value= Math.floor((full - used) / full * 100);
            }            
            return {
                str:value+"% ("+(full-used)+"MB/"+full+"MB)",
                value:value
            };
        },
        reloadData(){
            this.getData();
            this.getUserInfo();
        },
        changePage(page) {
            this.currentPage = page;
            this.getData(page);
        },
        async getData(page=1,limit=10){
            this.loading=true;
            let data = null;
            try{
                data = await axios.get(
                    this.URL + "buy/getUserCardList?page="+page+"&limit="+limit+"&code="+this.searchCode+"&status=&sort="+this.sort+"&order="+this.order,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch(err){
                this.loading=false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.loading=false;
            if(data?.data?.code != 1){
                this.$notify.error({
                    title: "",
                    message: data?.data?.msg,
                });
                return;
            }
            this.totalPage = data?.data?.data?.last_page ?? 1;
            this.cardsData = data?.data?.data?.data ?? [];
        },
        //
        async getUserInfo() {
            let res = await this.$root.checkLogin();
            if(!res){
                if(this.$route.path != "/"){
                this.$router.replace("/");
                }        
                this.$root.goLogin();
            }
            this.userInfo = res?.data ?? null;
        },
        //
        debouncedGetData: (function () {
            let timer = null;
            return function () {
                if (timer) clearTimeout(timer);
                timer = setTimeout(() => {
                    this.getData(1,this.limit);
                }, 500);
            }
        })(),
    },
    mounted() {
        this.$nextTick(() => {
            //
            this.getUserInfo();
            this.getData();
        });
    },
    beforeDestroy() {
        this.axiosC.abort();
    }
}
</script>

<style scoped lang="scss" src="@/assets/css/view/userPage.scss">

</style>