<template>
    <div id="menuBox">
        <!--  -->
        <div class="menuBox">
            <div class="logo">
                <img :src="logoPng"/>
                <img class="text" :src="fontPng"/>
            </div>
            <div class="userInfo">
                <el-tooltip effect="dark" :content="userInfo?.username ? userInfo?.username : ''" placement="bottom">
                    <div class="name">{{ userInfo?.username ? $root.shortStr(userInfo.username,5) : '' }}</div>
                </el-tooltip>
                
                <el-tooltip class="exitButton" effect="dark" content="" placement="bottom">
                    <div class="exit" @click="$root.logout()"></div>
                </el-tooltip>
            </div>
        </div>
        <!--  -->
        <div class="bottomBox">
            <ul>
                <li v-for="item in menus" :key="item.name" @click="linkTo(item)" :class="item.isActive?'active':''">
                    <i :class="'icon '+item.icon"></i>
                    <span>{{ item.name }}</span>
                </li>
            </ul>
            <!--  -->
             <div class="childContent" :style="childStyle">
                <ul>
                    <li v-for="item in childContent" :key="item.name" @click="linkTo(item)">
                        <div class="item">
                            <div class="name">
                                <i :class="'icon '+item.icon"></i>
                                <span>{{ item.name }}</span>
                            </div>
                        </div>
                    </li>
                </ul>
             </div>
        </div>
    </div>
</template>

<script>
import fontPng from "@/assets/img/font.png";
import logoPng from "@/assets/img/logo.png";

export default {
    name: 'menuBox',
    props: {
        userInfo:{
            type:Object,
            default:function(){
                return null
            }
        }
    },
    data() {
        return {
            fontPng:fontPng,
            logoPng:logoPng,
            childContent:[],
            childStyle:{},
            ceilNum: 3,
            rowHeight: 62,
            menus:[
                {
                    "name":"",
                    "url":"/usercenter",
                    "isParent":false,
                    "icon":"el-icon-s-home",
                    "isActive":false,
                },
                {
                    "name":"",
                    "isParent":true,
                    "icon":"el-icon-s-help",
                    "isActive":false,
                    "child":[
                        {
                            "name":"",
                            "url":"/servers",
                            "icon":"el-icon-s-unfold",
                            "content":""
                        },
                        {
                            "name":"",
                            "url":"/tunnel",
                            "icon":"el-icon-s-unfold",
                            "content":""
                        },
                        {
                            "name":"",
                            "url":"/download",
                            "icon":"el-icon-download",
                            "content":""
                        },
                        {
                            "name":"",
                            "url":"/domainauth",
                            "icon":"el-icon-s-claim",
                            "content":""
                        },
                        {
                            "name":"",
                            "url":"/ticket",
                            "icon":"el-icon-s-order",
                            "content":""
                        }
                    ]
                },
                {
                    "name":"",
                    "url":"/user",
                    "icon":"el-icon-user-solid",
                    "isParent":false,
                    "isActive":false,
                },
                {
                    "name":"",
                    "url":"/mall",
                    "icon":"el-icon-s-shop",
                    "isParent":false,
                    "isActive":false,
                },
                {
                    "name":"",
                    "url":"/help",
                    "icon":"el-icon-question",
                    "isParent":false,
                    "isActive":false,
                },
            ],
        }
    },
    watch: { 
        //
        $route: {
            handler() {
                this.menuActive(this.$route.path);
            },
            deep: true,
            immediate: true
        },
    },
    methods: {
        //
        menuActive(url=""){ 
            if(this.menus.length === 0){
                return;
            }
            this.menus.forEach(mainItem => {
                // 
                mainItem.isActive = false;

                // 
                if(mainItem.url === url){
                    mainItem.isActive = true;
                    return;
                }

                // 
                if(mainItem.child){
                    mainItem.isActive = mainItem.child.some(childItem => childItem.url === url);
                }
            });
        },
        //
        computeChildStyle(){
            let height = Math.ceil(this.childContent.length/this.ceilNum)*this.rowHeight;
            this.childStyle = {
                height:(height+50)+"px"
            };
            setTimeout(() => {
                this.childStyle = {...this.childStyle,overflow:"auto"}
                console.log(this.childStyle);
            }, 301);
        },
        linkTo(item){
            if(!item?.url||item?.isParent){
                this.openChildList(item);
                return;
            }
            let url = item?.url;
            this.hideChildList();
            this.$root.linkTo(url)
        },
        //
        openChildList(item){
            if(!item?.isParent)return;
            this.$root.enableMask(true,"61px",6,this.hideChildList);
            this.childContent = item?.child ?? [];
            this.computeChildStyle();
        },
        //
        hideChildList(){
            this.childStyle = "";
            this.$root.enableMask(false);
            setTimeout(() => {
                this.childContent = [];
            }, 301);
        },
    },
    mounted() {
        
    }
}
</script>

<style lang="scss" scoped src="@/assets/css/lib/menuBox.scss"></style>