<template>
  <div id="app">
    <div id="siteloading" class="siteloading">
      <div class="logoBox">
        <img class="logo" src="/logo.png"/>
      </div>
      <div class="textBox">
        <img class="icon loading-animation" src="/loading.svg"/>
        <div class="text hide"></div>
      </div>
    </div>

    <menuBox :userInfo="userInfo" v-if="$route.path!='/'"/>
    <div :class="$route.path!='/'?'routeView body':'routeView'">
      <router-view
      :style="$route.path=='/'?'':'margin-top:calc(60px + 1px + 40px);margin-bottom: 40px;'"
      /> <!--  -->
    </div>  
    <footerBox v-if="$route.path!='/'"/>  

    <!-- S -->
    <div ref="mask" class="mask" :style="maskStyle" v-show="mask" @click="clickMask"></div>
    <!-- E -->

    <!--  -->
    <loginBox v-if="isShowLogin" @hide="closeLogin" @appeal="openAppeal"/>

    <!--  -->
    <regBox v-if="isShowReg" @hide="isShowReg=false"/>

    <!--  -->
    <appealBox v-if="isShowAppealBox" @hide="closeAppeal" :reason="banReason" :username="banUsername"/>

    <div class="netError" v-if="isNetERR">
      <div class="title"></div>
      <div class="text"></div>
      <div class="btnGroup">
        <div class="btn" @click="refresh()">
          <img :src="logo"/>
        </div>
      </div>
    </div>

    <div class="loadingBg" v-if="loading">
      <div class="loading">
        <div class="imgBox">
          <img class="imgOuter loading-animation" :src="loadingIcon"/>
          <div class="text"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import logo from "@/assets/img/logo.png"
import loadingIcon from "@/assets/img/loading.svg"

import regBox from "@/components/lib/regBox.vue";
import loginBox from "@/components/lib/loginBox.vue";
import appealBox from "@/components/lib/appealBox.vue";
import menuBox from "@/components/lib/menuBox.vue";
import footerBox from "@/components/lib/footerBox.vue";

import axios from "axios";

export default {
  name: 'App',
  components: {
    regBox,
    loginBox,
    menuBox,
    footerBox,
    appealBox
  }, 
  data() {
    return {
      URL:window.config.apiUrl,
      isNetERR:false,
      isShowLogin:false,
      isShowReg:false,
      isShowAppealBox:false,
      logo:logo,
      loadingIcon:loadingIcon,
      scrollLock:false,
      mask:false,
      maskStyle:"",
      loading:false,
      userInfo: null,
      maskTimer:[],
      banReason:"",
      banUsername:"",
      clickMask:function(){ 
        return;
      }
    }
  },
  watch: {
    loading: {
      handler(val) {
        if (val) {
          this.mask = true;
          this.scrollLock = true;
        }
        else if(!this.loading && !this.isShowLogin && !this.isShowReg && !this.isShowAppealBox){
          let haveDialog = document.querySelector(".dialog") != null;
          if(!haveDialog){
            this.mask = false;
          }          
          this.scrollLock = false;
        }
      },
      immediate: true
    },
    "isShowLogin":function(val){
      if(val || this.isShowReg){
        this.mask = true;
        this.$root.scrollLock = true;
      }
      else if(!this.loading && !val && !this.isShowReg){
        this.mask = false;
        this.$root.scrollLock = false;
      }
    },
    "isShowReg":function(val){
      if(val || this.isShowLogin){
        this.mask = true;
        this.$root.scrollLock = true;
      }
      else if(!this.loading && !val && !this.isShowLogin){
        this.mask = false;
        this.$root.scrollLock = false;
      }
    },
    "isShowAppealBox":function(val){
      if(val || this.isShowReg){
        this.mask = true;
        this.$root.scrollLock = true;
      }
      else if(!this.loading && !val && !this.isShowReg){
        this.mask = false;
        this.$root.scrollLock = false;
      }
    },
    "isNetERR":function(val){
      if(val){
        this.mask = true;
        this.$root.scrollLock = true;
      }
      else{
        this.mask = false;
        this.$root.scrollLock = false;
      }
    },
    "$route.path":function(val){
      if(val != "/"){
        this.LocalGetUserInfo();
      }
    }
  },
  methods: {
    //
    refresh(){
      window.location.reload();
    },
    //
    netError(err=null){
      let status = err?.status ?? null;
      if(!status || status != 401){
        console.log(err);
        this.isNetERR = true;
        this.enableMask(true);
      }
    },
    //
    goLogin(){      
      this.isShowReg = false; 
      this.isShowLogin = true; 
    },
    //
    goReg(){      
      this.isShowLogin = false; 
      this.isShowReg = true; 
    },
    
    /**
     * /
     * @param isShow true:  false:
     * @param offset    0 60px 20% 50vh
     * @param zIndex  8
     * @param clickBack 
     */
    enableMask(isShow=true,offset=0,zIndex="8",clickBack=null){ 
      if(clickBack != null){
        this.clickMask = function(self=this){
          clickBack();
          self.clickMask = function(){ 
            return;
          };
        }
      }
      try{
        let timeout = 5;
        if(isShow){
            document.documentElement.style.overflow = "hidden";
            this.maskStyle = 'top:' +offset+';z-index:'+zIndex+';';
            this.mask = true;
        }
        if(!isShow){
          document.documentElement.style.overflow = "auto";
          this.$refs.mask.classList.remove("active");
          timeout = 300;
        }
        let timer = setTimeout(() => {
          if(isShow){
            this.$refs.mask.classList.add("active");  
          }
          else{
            this.mask = false;
            this.maskStyle = ""; 
          }
          //
          for(let i = 0; i < this.maskTimer.length; i++){
            clearTimeout(this.maskTimer[i]);
          }
        }, timeout); 
        this.maskTimer.push(timer);
      }
      catch(err){
        console.log("");
      }
    },
    //
    async getUserInfo(){
      let token = localStorage.getItem("token") ?? null;
      if(!token){
        return null;
      }
      //tokenheader
      let header = {headers:{token:token}};
      let data = null;
      try{
        data = await axios.get(this.URL+"/user/index",header);
      }
      catch(err){
        this.netError(err);
        return null;
      }
      return data.data ?? null;
    },
    //
    async logout(){
      localStorage.removeItem("token");
      window.location.reload();
    },
    //
    async checkLogin(){ 
      let hasUserInfo = await this.getUserInfo();
      if(!localStorage.getItem("token") || !hasUserInfo){
        return false;
      }
      return hasUserInfo;
    },
    //
    loadingEvent(isLoading=true){
      this.loading = isLoading;
    },
    //TOKEN
    getLocalToken(){
      let token = localStorage.getItem("token") ?? null;
      if(!token){
          if(this.$route.path != "/"){
              this.$router.replace("/");
          }
          this.$root.goLogin();
          return null;
      }
      return token;
    },
    //
    formatterDomains(str){
      let arr = JSON.parse(str);
      return arr
    },
    //
    linkTo(url,target="_self"){
      if(this.$route.path != url && target == "_self"){
        this.$router.replace(url);
      }
      if(target == "_blank"){
        window.open(url);
      }
    },
    //
    getDate(time) {
        let date = new Date(time*1000);
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let day = date.getDate();
        let hour = date.getHours();
        let minute = date.getMinutes();
        let second = date.getSeconds();
        //0
        month = month < 10 ? '0' + month : month;
        day = day < 10 ? '0' + day : day;
        hour = hour < 10 ? '0' + hour : hour;
        minute = minute < 10 ? '0' + minute : minute;
        second = second < 10 ? '0' + second : second;
        return year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second;
    },
    //
    shortStr(str,len=10){
      if(str.length <= len){
        return str;
      }
      return str.substring(0,len)+"...";
    },
    // 
    checkMob() {
      let ua = navigator.userAgent || navigator.vendor || window.opera;
      return /Android|iPhone|iPad|iPod|Windows Phone|webOS|BlackBerry|IEMobile|Opera Mini|Mobi/i.test(ua);
    },
    //
    setScollLock(isLock=true){
      this.scrollLock = isLock;
    },
    //S
    //
    async LocalGetUserInfo() {
      let res = await this.checkLogin();
      if(!res){
        if(this.$route.path != "/"){
          this.$router.replace("/");
          this.goLogin();
        }     
      }
      this.userInfo = res?.data ?? null;
    },
    //
    closeLogin(){
      this.isShowLogin=false;
    },
    //
    openAppeal(reason="",usernmae=""){
      console.log(reason,usernmae);
      this.banReason = reason;
      this.banUsername = usernmae;
      this.isShowAppealBox = true;
      this.closeLogin();
    },
    //
    closeAppeal(){
      this.isShowAppealBox = false;
    },
    //E
  },
  created(){
    // PC
    if(!this.checkMob() && window.config.pcPage && !window.config.DEBUG){
      window.location.href = window.config.pcPage;
    }
    this.LocalGetUserInfo();
  },
  mounted(){
        let text = [
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          ""
        ];

        let siteloading = document.getElementById("siteloading");
        if (!siteloading) return;
        let loadingText = siteloading.querySelector(".text");
        if (!loadingText) return;
        let index = 0;
        loadingText.innerHTML = text[index];
        loadingText.classList.remove("hide");
        let timer = setInterval(() => {
          loadingText.classList.add("hide");
          setTimeout(() => {
            index = (index + 1) % text.length;
            loadingText.innerHTML = text[index];
            loadingText.classList.remove("hide");
          }, 800);
        }, 5000); //  5 
      window.onload = () => {
        clearInterval(timer);
        siteloading.classList.add("hide");
        setTimeout(() => {
          siteloading.remove();
        }, 300);        
        console.log("");
      };

      //S
      this.$root.notifyOffet = 61;                            //
      this.$root.scrollLock = this.scrollLock;                //
      this.$root.setScollLock = this.setScollLock;            //
      this.$root.netError = this.netError;                    //
      this.$root.goLogin = this.goLogin;                      //
      this.$root.goReg = this.goReg;                          //
      this.$root.enableMask = this.enableMask;                //
      //this.$root.getUserInfo = this.getUserInfo;            //( checkLogin)
      this.$root.logout = this.logout;                        //
      this.$root.checkLogin = this.checkLogin;                //
      this.$root.loading = this.loadingEvent;                 //
      this.$root.getLocalToken = this.getLocalToken;          //token
      this.$root.formatterDomains = this.formatterDomains;    //
      this.$root.linkTo = this.linkTo;                        //
      this.$root.getDate = this.getDate;                      //
      this.$root.shortStr = this.shortStr;                    //
      this.$root.checkMob = this.checkMob;                    //
      //E
  }
}
</script>
<style lang="scss" src="@/assets/css/app.scss"></style>