<template>
    <div id="domainAuthPage" class="page">
        <div class="card">
            <div class="cardTitle"></div>

            <div class="toolsBar">
                <div class="row">
                    <el-alert
                        title="FRP"
                        type="error"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                </div>
                <div class="row">
                    <el-alert
                        title=""
                        type="error"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                </div>
                <div class="row">
                    <el-alert
                        title=""
                        type="error"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                </div>
                <div class="row">
                    <el-alert
                        title=" 5-10"
                        type="info"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                </div>
                <div class="row">
                    <el-alert
                        title="  "
                        type="error"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                </div>
                <div class="row">
                    <el-alert
                        :title="''+limit+' '"
                        type="info"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                </div>
            </div>

            <div class="toolsBar">
                <div class="row">
                    <div class="btnGroup">
                        <buttonLib btnClass="btn" text="" @click="openSubmitBox"/>
                        <buttonLib btnClass="btn" text="" @click="getData()"/>
                    </div>
                </div>
            </div>

            <div :class="loading?'tableBox loadingLocal':'tableBox'">
                <div class="empty" v-if="tableData.length==0"></div>
                <div class="tableLine" v-for="(item,key) in tableData" :key="'tableData_'+key">
                    <el-table :data="[item]" border style="width: 100%" :show-header="false"  row-class-name="tableTitle">
                        <el-table-column class-name="no-bottom-border" prop="domain" :resizable="false">
                        </el-table-column>
                    </el-table>
                    <el-table :data="[item]" border style="width: 100%" size="mini">
                        <el-table-column prop="status" label="" :resizable="false">
                            <template slot-scope="scope">
                                <el-tag v-if="scope.row.status == 0" type="success"></el-tag>
                                <el-tag v-if="scope.row.status == 1" type="info"></el-tag>
                                <el-tag v-if="scope.row.status == 2" type="danger"></el-tag>
                            </template>
                        </el-table-column>

                        <el-table-column label="" :resizable="false">
                            <template slot-scope="scope">
                                <div class="btnGroup">
                                    <buttonLib text="" class="btn" @click="del(scope.row)"/>
                                </div>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </div>

            <!-- S -->
            <dialogLib ref="dialog" title="" height="auto" max-width="400px" v-if="isShowConfirm" :isShowClose="false" @hide="cancelDel">
                <div class="confirm">
                    <div class="row">
                        <div class="title"> <span style="color: red;">{{ delName }}</span> ?</div>
                    </div>
                    <div class="row">
                        <el-alert
                            title=",."
                            type="error"
                            center
                            :closable="false">
                        </el-alert>
                    </div>
                    <div class="row">
                        <buttonLib btnClass="btn yes" text="" @click="doDel"/>
                        <buttonLib btnClass="btn no" text="" @click="cancelDel"/>
                    </div>
                </div>
            </dialogLib>
            <!-- E -->

            <!-- S -->
                <dialogLib ref="dialog" title="" height="auto" max-width="400px" :isShowClose="!submitLoading" v-if="isShowSubmitBox" @hide="closeSubmitBox">
                    <div :class="submitLoading?'form loadingLocal':'form'">
                        <div class="row">
                            <el-alert
                                title=""
                                type="error"
                                center
                                :closable="false">
                            </el-alert>
                        </div>
                        <div class="row">
                            <div class="title"></div>
                            <div class="inputBox">
                                <div class="pre">http://</div>
                                <input class="hasPre" v-model="form.domain" placeholder=""/>
                            </div>
                        </div>
                        <div class="row">
                            <buttonLib btnClass="btn" text="" @click="submit"/>
                        </div>
                    </div>
                </dialogLib>
             <!-- E -->
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import buttonLib from '@/components/lib/unit/buttonLib.vue';
import dialogLib from '@/components/lib/unit/dialogLib.vue';

export default {
    name: 'domainAuthPage',
    components: {
        buttonLib,
        dialogLib
    },
    data() {
        return {
            loading:false,
            submitLoading:false,
            tableData:[],
            URL:window.config.apiUrl,
            limit:0,
            userInfo:null,
            axiosC: new AbortController(),    // 
            isShowConfirm:false,
            delName:"",
            delId:"",
            isShowSubmitBox:false,
            form:{
                domain:"",
            }
        }
    },
    methods: {
        //
        async submit(){ 
            this.loading = true;
            let data = null;
            try {
                data = await axios.post(
                    this.URL + "frp/domainAuth",
                    {
                        domain:this.form.domain,
                    },
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.loading = false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.loading = false;
            if(data?.data?.code != 1){
                this.$notify.error({
                    title: '',
                    message: data?.data?.msg,
                });
                return;
            }
            this.$notify.success({
                title: '',
                message: "",
            });
            this.closeSubmitBox();
            this.getData();
        },
        //
        closeSubmitBox(){
            this.$refs?.dialog?.hide();
            this.isShowSubmitBox = false;
            this.form.domain = "";
        },
        //
        openSubmitBox(){
            this.isShowSubmitBox = true;
        },
        //
        cancelDel(){
            this.$refs.dialog.hide();
            this.isShowConfirm = false;
            this.delName = "";
            this.delId = "";
        },
        //
        del(row){
            this.delName = row.domain;
            this.isShowConfirm = true;
            this.delId = row.id;
        },
        //
        async doDel(){
            this.loading = true;
            let data = null;
            this.$refs.dialog.hide();
            this.isShowConfirm = false;
            this.delName = "";
            try {
                data = await axios.post(
                    this.URL + "frp/delDomain",
                    {
                        id:this.delId,
                    },
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
                this.delId = "";
            }
            catch (err) {
                this.loading = false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.loading = false;
            if(data?.data?.code != 1){
                this.$notify.error({
                    title: '',
                    message: data?.data?.msg,
                });
                return;
            }
            this.$notify.success({
                title: '',
                message: "",
            });
            this.getData();
        },
        //
        async getData() {
            this.loading = true;
            this.tableData = [];
            let data = null;
            try {
                data = await axios.get(
                    this.URL + "frp/domainsList",
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.loading = false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.loading = false;
            if(data?.data?.code != 1){
                this.$notify.error({
                    title: '',
                    message: data?.data?.msg,
                });
                return;
            }
            this.tableData = data?.data?.data?.data;
            this.limit = data?.data?.data?.limit;
        },
    },
    mounted() {
        this.$nextTick(() => { 
            this.getData();
        });
    },
    beforeDestroy() {
        this.axiosC.abort();
    }
}
</script>

<style scoped lang="scss" src="@/assets/css/view/domainAuthPage.scss">

</style>